/*  Clear  */
.clear:after {
  content: '';
  line-height: 0;
  display: table;
  clear: both; }

/* sr-only */
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

/*  Notext  */
.notext {
  white-space: nowrap;
  text-indent: 100%;
  text-shadow: none;
  overflow: hidden; }

.notext-old {
  font-size: 0;
  line-height: 0;
  text-indent: -4000px; }

/*  Hidden  */
[hidden],
.hidden {
  display: none; }

/*  Disabled  */
[disabled],
.disabled {
  cursor: default; }

