/* ------------------------------------------------------------ *\
	Pagination
\* ------------------------------------------------------------ */
.paging {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0; }
  @media (max-width: 767px), screen and (max-width: 812px) and (orientation: landscape) {
