        padding: 0;
        margin-bottom: 20px; } }
  .paging ul li {
    margin: 0 5px; }
    @media (max-width: 767px), screen and (max-width: 812px) and (orientation: landscape) {
      .paging ul li:first-child {
        margin-left: 0; } }
    @media (max-width: 767px), screen and (max-width: 812px) and (orientation: landscape) {
      .paging ul li:last-child {
        margin-right: 0; } }
  .paging ul li.paging__spacer,
  .paging ul a {
    background-color: #fff;
