    background-color: #373a3e; }
  .paging .paging__next,
  .paging .paging__prev {
    font-size: 16px;
    color: #000;
    transition: all .4s; }
    .paging .paging__next:hover, .paging .paging__next:hover,
    .paging .paging__prev:hover,
    .paging .paging__prev:hover {
