  .paging .paging__next:only-child {
    margin-left: auto;
    margin-right: 0; }
  .paging .paging__prev:only-child {
    margin-right: auto;
    margin-left: 0; }
  .paging .paging__label,
  .paging .paging__first,
  .paging .paging__last,
  .paging .paging__prev,
  .paging .paging__next {
