/* ------------------------------------------------------------ *\
	Shell
\* ------------------------------------------------------------ */

.shell {
	max-width: calc( #{$shell-max-width} + #{$shell-gutter} * 2);
	padding-left: $shell-gutter;
	padding-right: $shell-gutter;
	margin: auto;
	width: 100%;
}

/* Shell Fluid */

.shell--fluid {
	max-width: none;
}

/*shell--lg*/
.shell--lg {
	max-width: calc( #{$shell-max-width-lg} + #{$shell-gutter} * 2);
}
